
.animated-btn {
  color: #000000;
  display: inline-block;
  min-width: 154px;
  text-align: center;
  text-decoration: none;
  padding: 20px;
  position: relative;
  text-align: center;
  &:hover {
    color: #000000;
    text-decoration: none;
    .animated-btn-stroke:nth-child(2) {
      stroke-dashoffset: 0;
      stroke-opacity: 1; } } }


.animated-btn-stroke {
  display: block;
  width: calc(100% - 40px);
  height: 50px;
  stroke: #FBFAF7;
  position: absolute;
  left: 20px;
  top: 5px;
  stroke-width: 4;
  stroke-opacity: 0;

  &:nth-child(2) {
    stroke-dasharray: 650px;
    stroke-dashoffset: 650px;

    stroke: #3F9F7E;
    stroke-width: 5;
    transition: stroke-dashoffset 800ms ease-out; } }
