@import "../variables.sass";

.resource-page {
  overflow: auto;
  height: 100%;
  .resource-view {
    display: flex;
    position: relative;
    border-bottom: 1px solid #000;
    @media (max-width:$mq-mobile) {
      flex-direction: column; }
    .navigate-back {
      border-right: 1px solid #000;
      max-width: 50px;
      flex: 0 0 50px;
      display: flex;
      align-items: center;
      cursor: pointer;
      position: relative;
      .arrow-icon {
        height: 20px;
        width: 12px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
        position: absolute;
        top: 15px;
        left: 15px;
        transform: rotate(-180deg); }

      span {
        display: block;
        transform: rotate(-90deg);
        width: 100%;
        white-space: nowrap; } }


    .resource-preview {
      flex: 1 1 60%;
      height: 100%;
      position: sticky;
      top: 20px;
      margin-bottom: 60px;
      padding: 0px 20px;
      @media (max-width:$mq-mobile) {
        flex: 0 0 100%;
        margin-bottom: 40px;
        position: relative; }
      .iframe-container {
        position: relative;
        overflow: hidden;
        width: 100%;
        padding-top: 56.25%;
        iframe {
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          width: 100%;
          height: 100%; } }
      img {
        width: 100%;
        filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.5)); } }

    .resource-sideinfo-container {
      flex: 1 1 38%;
      margin-left: auto;
      padding: 0px 20px;
      margin-top: 20px;
      margin-bottom: 20px;
      @media (max-width:$mq-mobile) {
        flex: 0 0 100%; }
      a.mailto {
        text-align: center;
        font-size: 16px;
        line-height: 20px;
        color: #575449;
        width: 100%;
        display: inline-block; } } }

  .related-resources {
    text-align: center;
    margin: 30px 0px;
    .related-resources-panel {
      min-height: 320px;
      position: relative;
      .related-options {
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        span {
          margin: 0.35em; } } } } }
