.collection-view {
  padding: 30px;
  overflow: auto;
  height: 100%;
  text-align: center;
  .collection-header {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    h2 {
      font-family: "P22MackinacMedium";
      font-style: normal;
      font-weight: normal;
      font-size: 30px;
      text-align: center;
      color: #000000; }
    .export-link {
      position: absolute;
      right: 0px;
      color:#575449 {} } }
  .collection-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px; }
  .empty-collection {
    font-family: "P22MackinacMedium";
    font-size: 22px;
    line-height: 32px;
    text-align: center;
    color: #575449;
    margin-top: 20%; } }
